/* Google icon */
.google {
  margin-left: 10px;
}


/* eye icon */
.eye {
  position: absolute;
    right: 15px; 
    top: 50%;
    transform: translateY(-50%);
    cursor: pointer;
}

.submit-container{
  display: flex;
  gap: 30px;
  margin: 60px;
  width: 88%;
}
.gray{
  background: #eaeaea;
  color: black;
}


@media screen and (max-width: 425px) {
  .row {
    flex-direction: column;
    align-items: center; /* Center both columns vertically */
  }

  .column {
    max-width: 100%;
    border-radius: 0;
    margin-bottom: 20px; /* Add margin between the columns on mobile */
    border-bottom-left-radius: 30px;
    border-bottom-right-radius: 30px;
  }

  .column1 {
    margin-top: 30px;
    max-width: 100%;
  }

  .text1 {
    font-size: 60px;
    margin-top: 20px;
  }
  .qr {
    /* size: 250px; */
    /* margin-left: 40px; */
    max-width: 80%; 
      height: auto;
     margin-top: auto;
  }

  /* Additional CSS adjustments for mobile can be placed here */
}